/* Import the Orbitron font for the title */
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300&display=swap');

body {
  font-family: 'Orbitron', sans-serif;
  margin: 0;
  overflow: hidden;
  background-size: cover;
  height: 100vh;
}


/* Styling for the social links */
.social-links {
  display: flex;
  font-size: 50px !important;
  justify-content: center;
  gap: 25px;
  position: absolute;
  bottom: 0%;
  top: 150%;
  left: 50%;
  transform: translateX(-50%);
  width: 70%;
  padding: 10px;
}

/* Initial styles for social links with green-yellow background */
.social-links a {
  /* ... (your existing styles) */
  position: relative; /* Add relative positioning to the link container */
  text-decoration: none;
  font-size: 30px !important;
  font-family: 'Source Code Pro', monospace;
  box-shadow: inset 0 0 0 0 black;
  color: #4c3a6b;
  margin: 0 -.25rem;
  font-size: 20px;
  padding: 0 .25rem;
  line-height: 1.5;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out; /* Adjust the transition property */
}

/* Styling for the green-yellow background on the first part */
.social-links a span:first-child {
  background-color: #74e3a4; /* Green background color */
}

/* Styling for the yellow-green background on the second part */
.social-links a span:last-child {
  background-color: #d6a12e; /* Yellow background color */
}

/* Add a hover effect for social links */
.social-links a:hover span:first-child {
  background-color: #d6a12e; /* Change the green to yellow on hover */
}

.social-links a:hover span:last-child {
  background-color: #74e3a4; /* Change the yellow to green on hover */
}


.social-links {
  box-shadow: inset 0 0 0 0 #54b3d6;
  color: #54b3d6;
  padding: 0 .25rem;
  margin: 0 -.25rem;
  transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
}




#title-name {
  text-align: center;
  color: #4c3a6b;
  font-size: 80px;
  font-family: 'Source Code Pro', monospace;
  transition: transform 2s ease-in-out;
}

#subtitle {
  text-align: center;
  color: #4c3a6b;
  font-size: 30px;
  padding-bottom: 10px;
  font-weight: 100; /* Apply thin style */
  font-family: 'Source Code Pro', monospace;
  transition: transform 2s ease-in-out;
  top: 700%; /* Adjust the top property to move the subtitle closer to the title */
}

#info-1 {
